/*****************************************************************************/
/*
/* Tufte Jekyll blog theme
/* Based on Tufte CSS by Dave Liepmann ( https://github.com/edwardtufte/tufte-css )
/*
/*****************************************************************************/

/*--- Tufte.css ---*/

@charset "utf-8";

/* Réglages */ 

@import "../_sass/fonts";
@import "../_sass/settings";
@import "../_sass/syntax-highlighting";

/* Remise à zéro */

* { margin: 0; padding: 0; }

/* Réglages généraux */

html {
      vertical-align: baseline;
      font-size: 11px;
}

body {
      width: 95%;
      margin-left: auto;
      margin-right: auto;
      padding-left: 2.5%;
      padding-right: 2.5%;
      font-family: $body-font;
      background-color: $bg-color;
      color: $text-color;
      max-width: 1400px;
      counter-reset: sidenote-counter;
}

article {
      position: relative;
      padding-left: 1rem;
      padding-top: 0.5rem;
}

section {
      padding-top: 1rem;
      padding-bottom: 1rem;
}

/* Adaptation à la taille de l'écran */

@media screen and (min-width: 800px) { 
      html { font-size: 12px; }
      article { padding-left: 1rem; }
}

@media screen and (min-width: 900px) { 
      html { font-size: 13px; }
      article { padding-left: 2rem; }
}

@media screen and (min-width: 1000px) { 
      html { font-size: 14px; margin-left:1rem; margin-right:1rem }
      article { padding-left: 2.5rem; }
}

@media screen and (min-width: 1100px) { 
      html { font-size: 15px; margin-left:2rem; margin-right:2rem }
      article { padding-left: 3rem; }
}

@media screen and (min-width: 1200px) { 
      html { font-size: 16px; margin-left:3rem; margin-right:3rem }
      article { padding-left: 3.5rem; }
}

@media screen and (min-width: 1300px) {
      html{ font-size: 17px; margin-left:4rem; margin-right:4rem }
      article { padding-left: 4rem; }
}

/* Niveaux de titre */

h1 { 
      font-size: 2.5rem;
      font-weight: 400;
      line-height: 1;
      margin-top: 1.6rem;
      margin-bottom: 1.6rem;
}

h1.title {
      // font-family: $title-font;
      // font-style: normal;
      // color: $text-color;
}

h2 {       
      font-size: 2.1rem;
      font-weight: 400;
      font-style: italic;
      line-height: 1;
      margin-top: 1.9rem;
      margin-bottom: 0;
}

h3 { 
      font-size: 1.7rem;
      font-weight: 400;
      font-style: italic;
      line-height: 1; 
      margin-top: 2.2rem;
      margin-bottom: 0;
}

/* Paragraphes */

p {
      width: 60%;
      font-size: 1.4rem;
      line-height: 2rem;
      hyphens: auto;
      margin-top: 1.4rem;
      padding-right: 2rem;
      vertical-align: baseline;
}

p.subtitle { 
      font-size: 1rem;
      line-height: 1.5rem;
      color: $subtitle-color;
      font-family: $sans-font;
      font-style: Light;
      margin-top: -0.5rem;
      margin-bottom: 2rem;
      text-align: left;
}

/* Listes */

ol { 
      font-size: 1.4rem;
      padding-left: 2.5%;
      padding-right: 2.5%;
      -webkit-padding-start: 2.5%;
      -webkit-padding-end: 2.5%;
      list-style-type: inherit;
}      

ul { 
      font-size: 1.4rem;
      width: 40%;
      -webkit-padding-start: 2.5%;
      -webkit-padding-end: 2.5%;
      list-style-type: none;
}

li {
      line-height: 2rem;
      margin-top: 1rem;
      padding: 0.25rem 0;
      padding-right: 2rem;
      vertical-align: baseline;
}

/* Liens */

a {
      color: $contrast-color;
      text-decoration: none;
}

/* Blocs de citation */

blockquote { border-left: 2px solid $contrast-color; }

blockquote p, blockquote footer {  
      width: 55%;
      line-height: 1.75rem;
      padding-left: 2.5%;
      padding-right: 2.5%;
}

blockquote footer {  
      font-size: 1.1rem;
      line-height: 1.75;
      margin-top: 1.75rem;
      margin-bottom: 1.75rem;
      text-align: right;
}

/* Épigraphes */

div.epigraph { margin: 3.5rem 0; }

div.epigraph > blockquote {
      margin-top: 3rem;
      margin-bottom: 3rem;
}

div.epigraph > blockquote, div.epigraph > blockquote > p { 
      font-style: italic;
      border-left: none;
}

div.epigraph > blockquote > footer {  
      font-style: normal;
      width: 45%;
      padding-left: 2.5%;
      padding-right: 2.5%;
      padding-top: 0;
      margin-top: 0.5rem;
      margin-bottom: 3rem;
      border-left: none; 
}

div.epigraph > blockquote > footer > cite {
      font-style: italic;
      border-left: none;
}

/* Début de phrase */

span.newthought { font-variant: small-caps;
                  font-size: 1.2em;
                  letter-spacing: 0.05rem; }

span.smallcaps { font-variant: small-caps }

/* Images */

img { max-width: 100%; }

figure, figure img.maincolumn { 
      max-width: 55%;
      -webkit-margin-start: 0;
      -webkit-margin-end: 0;
      margin-bottom: 2em;
}

figcaption { 
      float: right;
      clear: right;
      margin-right: -50%;
      margin-top: 0;
      margin-bottom: 0;
      font-size: 1.0rem;
      line-height: 1.6;
      vertical-align: baseline;
      position: relative;
      max-width: 40%;
}

figure.fullwidth { margin-top: 2em}

figure.fullwidth figcaption { 
      float: left;
      clear: left;
      margin-left: 20%;
      margin-right: 20%;
      font-size: 1.0rem;
      line-height: 1.6;
      vertical-align: baseline;
      position: relative;
      max-width: 100%; 
}

/* Notes marginales */

.sidenote, .marginnote { 
      float: right;
      clear: right;
      margin-right: -50%;
      width: 40%;
      margin-top: 0;
      margin-bottom: 1.96rem;
      font-size: 1rem;
      line-height: 1.3;
      vertical-align: baseline;
      position: relative;
      hyphens: none; 
      text-align: left;
}

li .sidenote, li .marginnote { 
      margin-right: -150%;
      width: 85%;
}

blockquote .sidenote, blockquote .marginnote { 
      margin-right: -59%;
      width: 43.5%;
}

.sidenote-number { counter-increment: sidenote-counter; }

.sidenote-number:after, .sidenote:before { 
      content: counter(sidenote-counter) " ";
      font-family: et-bembo-roman-old-style;
      color: $contrast-color; //added color
      position: relative;
      vertical-align: baseline;
}

.sidenote-number:after { 
      content: counter(sidenote-counter);
      font-size: 1rem;
      top: -0.5rem;
      left: 0.1rem;
}

.sidenote:before {
      content: counter(sidenote-counter) ".\000a0"; // espace insécable
      color: $contrast-color;
      top: 0rem;
} // removed superscripting for numerical reference in sidenote

input.margin-toggle { display: none; }

label.sidenote-number { display: inline; }

label.margin-toggle:not(.sidenote-number) { display: none; }

/* Mobile */ 

@media screen and (max-width: 760px) { 
      p, footer { width: 92.5%; }
      pre code { width: 87.5%; }
      ul { width: 92.5%; }
      ol { width: 92.5% }
      figure { max-width: 90%; }
      figcaption, figure.fullwidth figcaption { margin-right: 0%; max-width: none; }
      blockquote p, blockquote footer, div.epigraph > blockquote > footer,.MJXc-display { 
            width: 90%;
            padding-left: 5%;
            padding-right: 5%;
      }
}

@media (max-width: 760px) { 
      label.margin-toggle:not(.sidenote-number) { display: inline; color: $contrast-color; }
      .sidenote, .marginnote { display: none; }
      .margin-toggle:checked + .sidenote,
      .margin-toggle:not(:checked) + .marginnote { 
            display: block;
            float: left;
            left: 1rem;
            clear: both;
            width: 95%;
            margin: 1rem 2.5%;
            vertical-align: baseline;
            position: relative;
      }
      label { cursor: pointer; }
      pre, pre code, p code, p pre code { width: 90%; padding: 0; }
      .table-caption { 
            display: block;
            float: right;
            clear: both;
            width: 98%;
            margin-top: 1rem;
            margin-bottom: 0.5rem;
            margin-left: 1%;
            margin-right: 1%;
            vertical-align: baseline;
            position: relative; 
      }
      div.table-wrapper, table, table.booktabs { width: 85%; }
      div.table-wrapper { border-right: 1px solid #efefef; }
      img { max-width: 100%; } 
      figcaption.maincolumn-figure, figure.fullwidth figcaption { float:none; text-align: center;}
      figure.fullwidth figcaption { margin-left: 0; }
}

/* Code */

.sans { 
      font-family: $sans-font;
      letter-spacing: .03em;
}

pre { width: 52.5%; //, pre code, p pre code are both selectors in tufte.css - they have been removed here to fix alignment issues
           padding-left: 2.5%;
           overflow-x: auto; }

pre, pre code, p code, p pre code { 
      font-family: $code-font;
      font-size: 1.25rem;                   
      line-height: 1.75;        
      margin-top: 1.75rem;
}

.marginnote code, .sidenote code { font-size: 1.1rem; } //more .code class removal

/* Tableaux */

table, table.booktabs { 
      width: auto;
      margin: 0 auto;
      border-spacing: 0px;
      border-top: 2px solid $border-color;
      border-bottom: 2px solid $border-color; 
}

.booktabs th, th { 
      border-bottom: 1px solid $border-color;
      padding: 0.65ex 0.5em 0.4ex 0.5em;
      font-weight: normal;
      text-align: center; 
}

th, td { 
      font-size: 1.2rem;
      line-height: 1.71428571;
}

.booktabs th.cmid, th { border-bottom: 1px solid lighten($border-color, 25%); }

.booktabs th.nocmid { border-bottom: none; }

.booktabs tbody tr:first-child td,  tr:first-child td { padding-top: 0.65ex; } /* add space between thead row and tbody */

.booktabs td, td { 
      padding-left: 0.5em;
      padding-right: 0.5em;
      text-align: left; 
}

.booktabs caption, caption { 
      font-size: 90%;
      text-align: left;
      width: auto;
      margin-left: auto;
      margin-right: auto;
      margin-top: 1ex;
      caption-side: top;
}

.booktabs tbody tr td.l { text-align: left !important; }
.booktabs tbody tr td.c { text-align: center !important; }
.booktabs tbody tr td.r { text-align: right !important; }

.table-caption { 
      float:right;
      clear:right;
      margin-right: -60%;
      width: 50%;
      margin-top: 0;
      margin-bottom: 0;
      font-size: 1.0rem;
      line-height: 1.96;
}

div.table-wrapper { overflow-x: auto; } //changed all overflow values to 'auto' so scroll bars appear only as needed

/*--- Modifications personnalisées ---*/

/* Références */

// div.references > div > p { text-align: left; } // width: 95% pour avoir les références sur toute la page

/* Pour aller plus loin */

// div.dossier > p { text-align: left; }

/* Nota bene */

// div.nota-bene > p { text-align: left; }

/* Microtypographie */

sup { vertical-align: top; position: relative; top: -0.5em; }

/* Image de titre */

.title-image {
      display: block;
      margin-top: 2rem;
      margin-left: auto;
      margin-right: auto;
      padding-right: 2.5%;
      width: 35%;
}

.print-image {
      display: block;
      margin-top: 1rem;
      margin-left: auto;
      margin-right: auto;
      padding-right: 2.5%;
      width: 30%;
}

/* Nav */

.flex-container {
      font-variant: small-caps;
      display: flex;
      justify-content: center;
      align-items: stretch;
      flex-wrap: wrap;
      align-content: space-between;
      font-size: 1.2rem;
      padding: 6px;
}
 
.flex-container a {
      text-decoration: none;
      display: inline-block;
      transition: .3s;
      margin: 6px;
}
 
.flex-container a:hover {
      color: $text-color;
}
 
.flex-container a.active {
      color: $text-color;
      cursor: default;
}

/* -- Jekyll specific styling --*/

/* Classes */

.contrast { 
      font-size: 1.4rem;
      color: $contrast-color;
      font-family: $title-font;
      font-style: normal;
      margin-bottom: 0.5rem
}

.smaller { 
      font-size: 1rem;
      color: $text-color;
      font-family: $sans-font;
      font-style: normal;
      line-height: 1.5rem;
      text-align: left;
}

/* Footer */

ul.footer-links, .credits {
      list-style: none;
      text-align: center;
      margin: 0 auto;
}

ul.footer-links li {
      display: inline;
      padding: 0.5rem 0.5rem;
}

.credits{
      font-family: $sans-font;
      padding: 0.5rem 3rem; 
      & a { color: $contrast-color; }
}

/* Plein écran */

.fullwidth, li.listing div{ max-width: 90%; }

.full-width { .sidenote, .sidenote-number, .marginnote { display: none; } }

body.full-width, .content-listing, ul.content-listing li.listing { 
      width: 95%;
      margin-left: auto;
      margin-right: auto;
      padding-top: 2.5%;
      padding-bottom: 0%;
      padding-left: 2.5%;
      padding-right: 2.5%;
      text-align: left;
      line-height: 1.5rem;
}

.full-width article p { width: 90%; }

/* Éléments de liste */

li.listing hr { width:100%; }

.listing, .listing h3 {
      display: inline-block;
      margin: 0;
}

li.listing {
  margin:0;
  & p{
    width: 100%
  }
}


li.listing:last-of-type {
      border-bottom: none;
      margin-bottom: 1.4rem;
}

li.listing h3.new {
      text-transform: uppercase;
      font-style: normal;
}

/* Ligne */

hr.slender {
      border: 0;
      height: 1px;
      margin-top: 1.4rem;
      margin-bottom:1.4rem;
      background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
      background-image:    -moz-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
      background-image:     -ms-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
      background-image:      -o-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
}

/* Impression */

@media screen { .print-header { display:none; } }

@media screen { .print-footer { display: none; } }

@media print {
      * {
            -webkit-transition: none !important;
            transition: none !important;
      }
      *,
      *:before,
      *:after {
            background: transparent !important;
            box-shadow: none !important;
            text-shadow: none !important;
      }
      @page {
            margin: 0.75in 0.5in 0.75in 0.5in;
            orphans:4; widows:2;
      }
      body { font-size: 12pt; }
      html body span.print-footer, .print-header {
            font-family: $sans-font;
            font-size: 9pt;
            margin-top: 22.4pt;
            padding-top: 4pt;
            border-top: 1px solid #000;
      }
      thead { display: table-header-group; }
      tr, img { page-break-inside: avoid; }
      img { max-width: 100% !important; }
      p, h2, h3 {
            orphans: 4;
            widows: 4;
      }
      article h2, article h2 h3, article h3 { 
              page-break-after: avoid; 
      }
      body header , body footer { display:none; }
      body footer::after {
            content: "l'Esprit européen. Tous droits réservés";
            display: block;
            text-align: center;
      } 
}

